.landing-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background: #f9f9f9;
  }
  
  .top-bar {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 20px;
    background: white;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  /* Navigation Links */
  .nav-links {
    display: flex;
    gap: 40px;
  }
  
  .nav-item {
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: bold;
    color: #ffffff;
    transition: 0.3s;
  }
  
  .nav-item:hover {
    color: #0056b3;
  }
  
  .scroll-container {
    padding: 30px;
    width: 100%;
    overflow-y: auto;
    justify-content: center;
    align-items: center;
  }
  
  .scroll-image {
    width: 80%;
    height: auto;
    border-radius: 15px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  }
  